<template>
  <div>
  <v-toolbar dense height="60px" color="error">
    <v-toolbar-title>
      <router-link to="/">
        <v-btn icon><v-icon size="35" color="white">mdi-home</v-icon></v-btn>
      </router-link>
    </v-toolbar-title>
  </v-toolbar>
  <v-container fluid class="bg-light">
    <v-layout align-center justify-center>
      <v-responsive class="mx-auto" max-width="344">
        <v-card class="my-5 border">
            <form @submit.prevent="businessLogin">
                <v-card-title>Login to Your Business Account</v-card-title>
                <v-card-text>
                  <v-text-field
                      v-model="businessForm.email"
                      hide-details="auto"
                      label="Email address"
                      placeholder="johndoe@gmail.com"
                      type="email"
                      :rules="emailRules"
                      required
                      prepend-inner-icon="mdi-email"
                  ></v-text-field>
                  <v-text-field v-model="businessForm.user_role" class="d-none" name="user_role"></v-text-field>
                  <v-text-field
                      v-model="businessForm.password"
                      label="Password"
                      type="password"
                      :rules="passwordRules"
                      required
                      hint="Enter your password to access this website"
                      prepend-inner-icon="mdi-lock"
                  ></v-text-field>
                  <div v-if="error" class="alert alert-danger">{{error}}</div>
                  <div class="text-md-end">
                    <router-link to="/forgetpassword" class="text-muted text-decoration-none">Forget Password</router-link>
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-btn type="submit" color="red" large block class="ml-0 text-white">Login</v-btn>
                </v-card-actions>
            </form>
        </v-card>
        <v-card-text class="text-center">
          <router-link to="/business/register">Create a New Account</router-link>
          <v-icon>mdi-arrow-right</v-icon>
        </v-card-text>
      </v-responsive>
    </v-layout>
  </v-container>
  </div>
</template>

<script>
import axios from 'axios';
// import { EventBus } from "@/event-bus";
export default {
  name: "BusinessLogin",
  metaInfo: {title: 'Business Login'},
    data() {
        return{
            businessForm:{
                email:'',
                password:'',
                user_role: '1',
            },
            error: '',
            valid: false,
            snackbar: false,
            text: '',
            timeout: '',
          emailRules: [
            v => !!v || 'E-mail is required.',
            v => /.+@.+/.test(v) || 'E-mail must be valid.',
          ],
          passwordRules: [
            v => !!v || 'Password is required.',
            v => /[A-Z]/.test(v) || 'Use upper case.',
            v => /[a-z]/.test(v) || 'Use lower case.',
            v => /[0-9]/.test(v) || 'Use number.',
            v => /[#?!@$%^&*-]/.test(v) || 'Use special character.',
            v => v.length >= 8 || 'Password must be greater than 8 characters.',
            v => v.length <= 15 || 'Password must be less than 15 characters.',
          ],
        }
    },
  created() {
    this.isAuthenticated();
    this.user_role();
    if( this.isAuthenticated() == true){
      if(this.user_role() == 1){
        this.$router.push({name: 'businessdashboard'})
      }
      if(this.user_role() == 0){
        this.$router.push({name: 'userlogin'})
      }
      if(this.user_role() == 2){
        this.$router.push({name: 'resellerlogin'})
      }
      if(this.user_role() == 3){
        this.$router.push({name: 'admin'})
      }
      if(this.user_role() == 4){
        this.$router.push({name: 'dashboard'})
      }
    }
    else {
      this.$router.push({name: 'businesslogin'});
    }

  },
  methods: {
      async businessLogin() {
        // Make API call to authenticate the user and obtain token and user data
        await axios.post('business/login', this.businessForm)
            .then((resp) => {
              this.token = resp.data.token;
              this.user = resp.data.user;
              const token = this.token;
              const userData = this.user;
              this.$store.dispatch('login', { token, userData });
              this.$router.push('profile');
              location.reload();
            })
            .catch((err)=>{
              console.log(err);
              this.error = err.response.data.message;
            })
      },
    isAuthenticated() {
      return this.$store.state.isAuthenticated;
    },
    user_role() {
      return this.$store.state.userData.user_role;
    },
    }
}
</script>

